.lead-card {
	width: 100%;

	&.active {
		border: 1px;
		border-top: 7px;
		border-style: solid;
		border-color: rgba(238, 0, 136, 0.5);
	}

	&.pending {
		border: 1px;
		border-top: 7px;
		border-style: solid;
		border-color: var(--general-2);
	}

	&.rejected {
		border: 1px;
		border-top: 7px;
		border-style: solid;
		border-color: var(--error-2);
	}

	&.hot-lead {
		border: 1px;
		border-top: 7px;
		border-style: solid;
		border-color: var(--alert-3);
	}
	.fs-12px {
		font-size: 12px !important;
	}
}

.lead-id {
	span {
		color: var(--general-5);
	}

	&.hot-badge {
		border-radius: 4px;
		background-color: var(--alert-3);
		padding: 8px;

		span {
			color: var(--basic-1);
		}
	}
}

.hot-pill {
	width: fit-content;
	border-radius: 24px;

	span {
		font-size: 10px;
	}
}
